import React from "react"
import styled from "styled-components"
import AnchorLink from "react-anchor-link-smooth-scroll"
import Google from "../../images/google_assistant.png"
import Alexa from "../../images/alexa.png"
import Siri from "../../images/siri.png"
import Img from "../../images/img.png"


import { Section, Container } from "../global"
import GatsbyImage from "gatsby-image"

const Integrations = () => (
  <Section id="start">
    <StyledContainer>
      <SectionTitle>Integrate with voice control systems</SectionTitle>
      <FeaturesGrid>
        <FeatureItem style={{textAlign: "center"}}>
        <AnchorLink  offset='150' href="#google">
        <img src={Google} alt="Google" style={{width: "50%", display: "inline-block"}}/>  
        </AnchorLink>
          <FeatureTitle>Google Asisstant</FeatureTitle>
          <FeatureText>
            Integrate with Google Assistant and Google Home
          </FeatureText>
        </FeatureItem>
        <FeatureItem style={{textAlign: "center"}}>
        <AnchorLink  offset='150' href="#alexa">
        <img src={Alexa} alt="Alexa" style={{width: "65%", display: "inline-block"}}/>  
        </AnchorLink>
          <FeatureTitle>Alexa</FeatureTitle>
          <FeatureText>
            Integrate with Alexa
          </FeatureText>
        </FeatureItem>
        <FeatureItem style={{textAlign: "center"}}>
        <AnchorLink  offset='150' href="#siri">
        <img src={Siri} alt="Siri" style={{width: "60%", display: "inline-block"}}/>  
        </AnchorLink>
          <FeatureTitle>Siri.</FeatureTitle>
          <FeatureText>
            Integrate with Siri
          </FeatureText>
        </FeatureItem>
      </FeaturesGrid>
       <Subtitle>Please feel free to contact <a href="mailto:support@arcoinnovation.com">support@arcoinnovation.com</a> in case you need assistance during integration.</Subtitle>
    </StyledContainer>

    <StyledContainer>
      <FeaturesGridHeader id="google">
        <FeatureItem>
        <SectionTitle>Integrate with Google Assistant</SectionTitle>
        </FeatureItem>
        </FeaturesGridHeader>
      <FeaturesGrid>
        <FeatureItem>
        <img src={Img} alt="Power_up" style={{width: "85%"}}/>  
          <FeatureTitle>Open an account at IFTTT</FeatureTitle>
          <FeatureText>
            a) Visit the <a href="https://ifttt.com/home">https://ifttt.com/home</a> 
            <br></br>
            b) If you haven’t already, sign in or sign up for an free account
          </FeatureText>
        </FeatureItem>
        <FeatureItem>
        <img src={Img} alt="Connect_to_Wifi" style={{width: "80%"}}/>  
          <FeatureTitle>Connect your account</FeatureTitle>
          <FeatureText>
            a) At the top right, click Search. Search for "Google Assistant" under services."
          </FeatureText>
        </FeatureItem>
        <FeatureItem>
        <img src={Img} alt="Choose_Wifi" style={{width: "80%"}}/>  
          <FeatureTitle>Configure wifi credentials </FeatureTitle>
          <FeatureText>
           a) Choose a Google Account to give IFTTT access to. Make sure to choose the account you used to set up Google Home or the Google Assistant on your phone.
           <br></br>
          </FeatureText>
        </FeatureItem>
      </FeaturesGrid>


     
      <Subtitle>Please feel free to contact <a href="mailto:support@arcoinnovation.com">support@arcoinnovation.com</a> in case you need assistance during installation.</Subtitle>

    </StyledContainer>

  </Section>

)

export default Integrations

const StyledContainer = styled(Container)`
margin: 80px auto;`

const SectionTitle = styled.h3`
  color: ${props => props.theme.color.primary};
  display: flex;
  justify-content: center;
  margin: 0 auto 40px;
  text-align: center;
`

const Subtitle = styled.h5`
  font-size: 16px;
  color: ${props => props.theme.color.accent};
  letter-spacing: 0px;
  margin-bottom: 12px;
  text-align: center;
`

const FeaturesGrid = styled.div`
  max-width: 1100px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0px auto;
  grid-column-gap: 40px;
  grid-row-gap: 35px;
  @media (max-width: ${props => props.theme.screen.sm}) {
    grid-template-columns: 1fr;
    padding: 0 30px;
  }
`

const FeaturesGridHeader = styled.div`
  max-width: 1100px;
  display: grid;
  grid-template-columns: 1fr;
  margin: 0px auto;
  grid-column-gap: 40px;
  grid-row-gap: 35px;
  @media (max-width: ${props => props.theme.screen.sm}) {
    grid-template-columns: 1fr;
    padding: 0 64px;
  }
`

const FeatureItem = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`

const FeatureTitle = styled.h4`
  color: ${props => props.theme.color.primary};
  letter-spacing: 0px;
  text-align: center;
  line-height: 30px;
  margin-bottom: 10px;
`

const FeatureText = styled.p`
  height: 50%;
`
